@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&family=Roboto:ital,wght@0,400;0,700;1,300&display=swap);



body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  
}

font-family: 'Open Sans', sans-serif;
font-family: 'Roboto', sans-serif;

h1,h2,h3,h4,h5,h6 {
  font-family: 'Open Sans', sans-serif !important;
}
p {
  font-family: 'Roboto', sans-serif !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.pt-8, .py-8 {
    padding-top: 7rem!important;
}
.pb-8, .py-8 {
    padding-bottom: 7rem!important;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav.navbar.navbar-expand-lg.navbar-light.bg-light {
  background: transparent !important;
}

.nav-container button.btn.btn-secondary {
  background: #44BBCC;
  font-family: Open Sans, sans-serif;
  border: none;
  outline: none !important;
}

.nav-container button.btn.btn-primary {
    outline: none !important;
    font-family: Open Sans, sans-serif;
    background: transparent;
    border: none;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #000000;
    margin-right: 20px;
}

.nav-container .mr-auto.navbar-nav a.nav-link {
    font-family: Open Sans, sans-serif;
}

.banner-container img.banner-image {
    width: 100% ;
}

.banner-container h1.display-3.text-center.text-md-left {
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 3.4rem;
    line-height: 87px;
    display: flex;
    align-items: center;
    color: #000000;
}

.banner-container .text-center a.btn.btn-primary {
    background: #44BBCC;
    border: none;
}

.form-section .container {
    background: #4F3C6A;
    padding: 30px 60px;
    z-index: 999;
    position: relative;
    bottom: -7px;
}

.form-section button.btn.btn-primary {
    background: #44BBCC;
    border: none;
    margin-left: 30px;
    margin-right: 10px;
    padding: 14px 30px;
}

.form-section form input.form-control {
    height: 59px;
}

.form-section .submit-container {
    display: flex;
    align-items: center;
}

.about-container {
    background: #EFF1F6;
}

.about-container .icon {
    height: 200px;
}

.about-container .card {
  padding: 60px 20px;
}

.about-container button.btn{
    width: 222px;
    height: 52px;
    background: #44BBCC;
    border-radius: 10px;
    color: #fff;
    margin: 0 auto;
}

.about-container .container-fluid {
    background: #4f3c6a;
}

.about-container .container-fluid h2 {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 46px;
  line-height: 54px;
  text-align: center;
  color: #fff;
}

footer {
    background: #352F2F;
    color: #fff;
}

